import { Injectable } from '@angular/core';
import { Response } from '@angular/http';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import 'rxjs/add/operator/map'

import { APPCONFIG } from '../config';
import { Auth } from '../_models/auth';
import { User } from '../_models/user';

@Injectable()
export class AuthenticationService {

  constructor(private http: HttpClient) {
    APPCONFIG.session = JSON.parse(localStorage.getItem('session'));
  }

  login(data): Observable<boolean> {
    return this.http.post<Auth>('login', data).map(response => {
      // login successful if there's a jwt token in the response
      if (response.token) {
        APPCONFIG.session = response
        localStorage.setItem('session', JSON.stringify(response));
        return true;
      } else {
        return false;
      }
    });
  }

  logout(): void {
    APPCONFIG.session = null;
    localStorage.removeItem('session');
  }

  check(): Observable<User> {
    return this.http.get<User>('check');
  }
}


