import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import 'rxjs/add/operator/map'
import 'rxjs/add/operator/catch'

import { AuthenticationService } from './authentication.service';
import { UserCreate } from '../_models/user/create';
import { UserFind } from '../_models/user/find';

@Injectable()
export class UserService {
  constructor(
    private http: HttpClient,
    private auth: AuthenticationService) {
  }

  create(data): Observable<UserCreate> {
    return this.http.post<UserCreate>('user/create', {
      email: data.email,
      password: data.password,
    }).map(response => {
      return response;
    }).catch(err => {
      return Observable.throw(err);
    });
  }

  find(data): Observable<UserFind[]> {
    return this.http.get<UserFind[]>('user/find', {}).map(response => {
      return response;
    }).catch(err => {
      return Observable.throw(err);
    });
  }
}


