import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material';
import { CHARTCONFIG } from '../charts/charts.config';

import { UserService } from '../_services/user.service'
import { DashboardTransferDialogComponent } from './components/transfer-dialog.component';

@Component({
  selector: 'my-dashboard',
  template: require('./dashboard.component.html')
})
export class DashboardComponent implements OnInit {
  constructor(
    private userService: UserService,
    private dialog: MatDialog,
  ) {}

  balance: number
  config = CHARTCONFIG;
  currency = [{
    code: 'EUR',
    name: 'Euro'
  }, {
    code: 'MXN',
    name: 'Mexican Peso'
  }, {
    code: 'USD',
    name: 'US Dollar'
  }]
  transfer = {
    sourceAmount: 10.00,
    destCurrency: 'EUR',
    destAmount: 10.00
  }

  ngOnInit() {
    this.balance = 399000.00;
  }

  submit() {
    const dialogRef = this.dialog.open(DashboardTransferDialogComponent, {
      data: this.transfer,
    });
    dialogRef.afterClosed().subscribe(result => {
      console.log(result);
    });
  }
}



