import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';

import ConfirmService from '../../_api/sw/confirm/service'
import TransferService from '../../_api/sw/transfer/service'

@Component({
  selector: 'dashboard-transfer-dialog',
  template: require('./transfer-dialog.component.html')
})
export class DashboardTransferDialogComponent implements OnInit {
  constructor(
    private dialogRef: MatDialogRef<DashboardTransferDialogComponent>,
    @Inject(MAT_DIALOG_DATA) private data: any,
    private confirmService: ConfirmService,
    private transferService: TransferService,
  ) {}

  quote;

  ngOnInit() {
    const transferInput = {
      currency: this.data.destCurrency,
      amount: this.data.destAmount,
    };
    this.transferService.run(transferInput).subscribe(result => {
      this.quote = {
        sw: result,
      };
    }, e => {});
  }

  confirm() {
    const input = {
      id: this.quote.sw.id,
    };
    this.quote.confirm = {}
    this.confirmService.run(input).subscribe(result => {
      this.quote.confirm.sw = result;
    }, e => {});
  }
}



